import React from 'react';
import {NavLink, Redirect} from 'react-router-dom';

import './../../../assets/scss/style.scss';
import Aux from "../../../hoc/_Aux";
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
import DEMO from "../../../store/constant";
import { validateEmail, validateName } from '../validators';
import { AuthConsumer } from '../../../App/contexts/AuthContext';
import config  from '../../../config';

const SignUpURL = config.baseURL + '/api/auth/register';

function SignUpWrapper() {
    return (
        <AuthConsumer>
            {(context) => {
                const { authState, addUserToAuthContext } = context;
                if (authState.loading) {
                    return (
                        <Aux>
                            <Breadcrumb />
                        </Aux>
                    );
                } else if (authState.isAuthenticated) {
                    return (
                        <Redirect to={'/profile'} />
                    );
                } else {
                    return (
                        <SignUp1 addUserToAuthContext={addUserToAuthContext} />
                    );
                }
            }}
        </AuthConsumer>
    )
}

class SignUp1 extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            errors: [],
        }

        this.signUp = this.signUp.bind(this);
    }

    signUp(e) {
        // validate data
        const firstName = document.getElementById("sign-up-fname").value.trim();
        const lastName = document.getElementById("sign-up-lname").value.trim();
        const email = document.getElementById("sign-up-email").value.trim();
        const password = document.getElementById("sign-up-password").value.trim();
        const cpassword = document.getElementById("sign-up-cpassword").value.trim();

        const tempErrors = [];

        if (!validateName(firstName)) {
            tempErrors.push("First Name cannot be empty");
        }
        if (!validateName(lastName)) {
            tempErrors.push("Last Name cannot be empty");
        }
        if (!validateEmail(email)) {
            tempErrors.push("Invalid Email");
        }

        const passwordCheckbox = [];
        if (password.match(/^[A-Za-z0-9\s]*$/) !== null) {
            passwordCheckbox.push("needs special character");
        }
        if (password.length === 0) {
            passwordCheckbox.push("is empty");
        }
        if (password.match(/[a-z]/g) === null) {
            passwordCheckbox.push("needs lowercase");
        }
        if (password.match(/[A-Z]/g) === null) {
            passwordCheckbox.push("needs uppercase");
        }
        if (password.match(/[0-9]/g) === null) {
            passwordCheckbox.push("needs digit");
        }

        if (passwordCheckbox.length > 0) {
            tempErrors.push('Invalid Password: ' + passwordCheckbox.join(', '));
        }

        if (password !== cpassword) {
            tempErrors.push('Passwords do not match');
        }

        if (tempErrors.length > 0) {
            console.log(tempErrors);
            this.setState({ errors : tempErrors});
            return;
        }

        const signup_payload = {
            first_name: firstName,
            last_name: lastName,
            email: email,
            password: password,
        };

        // make request
        fetch(SignUpURL, {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(signup_payload),
        })
            .then(response => {
                if (response.status !== 200) {
                    throw Error("User with email already exists");
                } else {
                    return response.json();
                }
            })
            .then(data => {
                this.props.addUserToAuthContext(data.user, data.token);
            })
            .catch((error) => {
                console.log(error);
            });
    }
    
    render () {

        return(
            <Aux>
                <Breadcrumb/>
                <div className="auth-wrapper">
                    <div className="auth-content">
                        <div className="auth-bg">
                            <span className="r"/>
                            <span className="r s"/>
                            <span className="r s"/>
                            <span className="r"/>
                        </div>
                        <div className="card">
                            <div className="card-body text-center">
                                <div className="mb-4">
                                    <i className="feather icon-user-plus auth-icon"/>
                                </div>
                                <h3 className="mb-4">Sign up</h3>
                                <div className="input-group mb-3">
                                    <input id="sign-up-fname" type="text" className="form-control" placeholder="First Name"/>
                                </div>
                                <div className="input-group mb-3">
                                    <input id="sign-up-lname" type="text" className="form-control" placeholder="Last Name"/>
                                </div>
                                <div className="input-group mb-3">
                                    <input id="sign-up-email" type="email" className="form-control" placeholder="Email"/>
                                </div>
                                <div className="input-group mb-4">
                                    <input id="sign-up-password" type="password" className="form-control" placeholder="Password"/>
                                </div>
                                <div className="input-group mb-4">
                                    <input id="sign-up-cpassword" type="password" className="form-control" placeholder="Confirm Password"/>
                                </div>
                                <div className="form-group text-left">
                                    <div className="checkbox checkbox-fill d-inline">
                                        <input type="checkbox" name="checkbox-fill-2" id="checkbox-fill-2"/>
                                            <label htmlFor="checkbox-fill-2" className="cr">Send me the <a href={DEMO.BLANK_LINK}> Newsletter</a> weekly.</label>
                                    </div>
                                </div>
                                <button className="btn btn-primary shadow-2 mb-4" onClick={this.signUp}>Sign up</button>
                                <p className="mb-0 text-muted">Allready have an account? <NavLink to="/auth/signin-1">Login</NavLink></p>
                            </div>
                        </div>
                    </div>
                </div>
            </Aux>
        );
    }
}


export default SignUpWrapper;